#home{
  max-width: 1000px;
  margin: auto;
  //margin padding 设置为4的倍数，方便管理
  >header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px;
    border-bottom: 1px solid #ddd;
  }
  >main{
    display: flex;
    padding: 8px;
    align-items: flex-start;
  }

}


@media (max-width:700px){
  #home >main{
    display: block;
    padding: 16px;
    > *{
      width: 100%;
      margin: 8px 0;
    }
  }
}